<template>
  <v-container fluid>
    <v-card v-if="isUsed !== null">
      <v-card-title>
        <h2>Code</h2>
      </v-card-title>
      <v-card-text>
        <v-text-field
          :value="code"
        />
      </v-card-text>
      <v-card-actions>
        <v-btn color="success" @click="copy()">Copy</v-btn>
      </v-card-actions>
    </v-card>
    <v-card v-else>
      <v-card-title>
        <h1>Loading...</h1>
      </v-card-title>
    </v-card>
    <v-snackbar
      :timeout="2000"
      top
      v-model="isCopying"
    >
      Copied
      <v-btn flat color="pink" @click.native="isCopying = false">Close</v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
export default {
  props: ['code'],
  data: () => ({
    isUsed: null,
    isCopying: null,
    result: null
  }),
  methods: {
    copy () {
      this.isCopying = true
      let input = document.createElement('input')
      input.value = this.result
      input.id = 'inp'
      document.body.appendChild(input)
      let text = document.getElementById('inp')
      text.select()
      document.execCommand('copy')  
      text.remove()
    }
  },
  created () {
    axios.get(`/satoshi/check_token?token=${this.code}`).then((res) => {
      let result = res.data
      if (result.success) {
        this.isUsed = (result.message.is_used === 0) ? false : true
        this.result = result.message.token
      } else {
        this.$router.push('/')
      }
    })
  }
}
</script>

